import React, { useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import "./App.css";

import profileImage from "./imgs/profile.jpg";
import githubIcon from "./imgs/github.png";
import instagramIcon from "./imgs/instagram.png";

import { posts } from "./postData";

const Home = () => {
	const profileCardRef = useRef(null);

	useEffect(() => {
		const target = profileCardRef.current;
		const initialTop = 10; //   

		const onScroll = () => {
			target.style.transform = `translateY(${initialTop}px)`;
		};

		onScroll(); //    
		window.addEventListener("scroll", onScroll);

		return () => {
			window.removeEventListener("scroll", onScroll);
		};
	}, []);

	return (
		<div className="App">
			<div className="container">
				<div className="main">
					<h1>H4RU Blog</h1>
					<h3>Just Talking Anything I think</h3>
					{posts.map((post) => (
						<div key={post.id} className="post-card">
							<h2>{post.title}</h2>
							<p className="post-card-content">{post.content}</p>
							<div className="post-footer">
								<div className="post-date">{post.date}</div>
								<Link to={`/post/${post.id}`}>Read More</Link>
							</div>
						</div>
					))}
				</div>
				<div className="sidebar">
					<div ref={profileCardRef} className="profile-card">
						<img
							className="profile-image"
							src={profileImage}
							alt="Profile"
						/>
						<h2>H4RU</h2>
						<p>
							Hi, I'm H4RU, a software engineer and blogger. This
							is my personal blog where I share my thoughts on
							various topics.
						</p>
						<div className="social-icons">
							<a
								href="https://github.com/H4RURAKA/"
								target="_blank"
								rel="noopener noreferrer">
								<img
									className="icon"
									src={githubIcon}
									alt="GitHub"
								/>
							</a>
							<a
								href="https://www.instagram.com/haruraka/"
								target="_blank"
								rel="noopener noreferrer">
								<img
									className="icon"
									src={instagramIcon}
									alt="Instagram"
								/>
							</a>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Home;
